.home {
  padding: 1rem 30%;
  min-height: 100vh;
  background: url('./../Assets/bg.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  overflow: auto;
}
.profile {
  margin: 1rem;
  /* border: 2px solid white; */
  flex-grow: 1;
  height: 30rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
}
.profile img {
  width: auto;
  height: 80%;
  border: 0.4rem solid aliceblue;
  border-radius: 100%;
}
.profile h1 {
  margin-top: 1rem;
  /* border: 2px solid white; */
  text-align: center;
  color: aliceblue;
}
.about {
  margin: 1rem;
  padding: 2rem;
  flex-grow: 1;
  /* border: 2px solid white; */
  overflow: auto;
}
.about h1 {
  font-size: 2.2rem;
  margin-bottom: 1rem;
}
.about h2 {
  font-size: 1.8rem;
  margin: 5rem 0 1rem 0;
}
.about p {
  font-size: 1.5rem;
  margin-top: 1rem;
  line-height: 2.5rem;
  text-align: justify;
}
.about ul {
  list-style: inside;
  font-size: 1.5rem;
  margin-top: 1rem;
  line-height: 2.5rem;
}

.contact h1 {
  position: fixed;
  bottom: 0;
  right: 0;
  font-size: 5rem;
  color: green;
  margin: 3rem;
  cursor: pointer;
}
.contact h1:hover {
  color: green;
}

/* --------------Media query--------------- */

@media screen and (max-width: 768px) {
  html {
    font-size: 80%;
  }

  .home {
    padding: 0.2rem;
    display: flex;
    flex-direction: column;
    background-attachment: scroll;
  }
  .profile {
    margin: 0.1rem;
    border: none;
  }
  .about {
    margin: 1rem 0.1rem;
    border-left: none;
    border-right: none;
    border-bottom: none;
  }
  .about p {
    text-align: left;
  }
  .contact h1 {
    font-size: 4rem;
  }
}
